import './style.css';
import { useState } from 'react';

const NftCrt = ({ createNft, ComponentChange }) => {
    const [NftTitle, setNftTitle] = useState('');
    const [NftContent, setNftContent] = useState('');

    const handleCreateNft = () => {
        createNft(NftTitle, NftContent);
        setNftTitle('');
        setNftContent('');
        ComponentChange('NftTap'); // NftCrt NftTap 
    };

    return (
        <div className="NftCrt">
            <h2 className='brd_topTitle'>  </h2>
            <div className='brd_titleOncheck'>
                <input
                    type="text"
                    placeholder=" "
                    value={NftTitle}
                    onChange={(e) => setNftTitle(e.target.value)}
                />
                <div className='brd_CrtBtn'>
                    <div className='brd_P' onClick={() => ComponentChange('NftTap')}><i></i></div>
                    <button className='brd_P' onClick={handleCreateNft} ><i></i> </button>
                </div>
            </div>
            <textarea
                placeholder=" "
                value={NftContent}
                onChange={(e) => setNftContent(e.target.value)}
            ></textarea>
        </div>
    );
};

export default NftCrt;
